"use client";

import { SidebarTrigger } from '@/components/ui/sidebar';
import { UserNav } from '@/components/user-nav';
import { usePathname } from 'next/navigation';
import {
  Breadcrumb,
  BreadcrumbItem,
  BreadcrumbLink,
  BreadcrumbList,
  BreadcrumbPage,
  BreadcrumbSeparator,
} from "@/components/ui/breadcrumb";
import React from 'react';

export function Header() {
  const pathname = usePathname();
  const segments = pathname.split('/').filter(Boolean);

  const formatSegment = (segment: string) => {
    return segment.charAt(0).toUpperCase() + segment.slice(1);
  };

  return (
    <header className="sticky top-0 z-30 flex h-14 items-center gap-4 border-b bg-background/95 backdrop-blur supports-[backdrop-filter]:bg-background/60 px-4 md:px-6">
      <div className="md:hidden">
        <SidebarTrigger />
      </div>
      <div className="flex-1">
        <Breadcrumb>
          <BreadcrumbList>
            {segments.map((segment, index) => (
              <React.Fragment key={segment}>
                <BreadcrumbItem>
                  {index < segments.length - 1 ? (
                    <BreadcrumbLink href={`/${segments.slice(0, index + 1).join('/')}`}>
                      {formatSegment(segment)}
                    </BreadcrumbLink>
                  ) : (
                    <BreadcrumbPage>{formatSegment(segment)}</BreadcrumbPage>
                  )}
                </BreadcrumbItem>
                {index < segments.length - 1 && <BreadcrumbSeparator />}
              </React.Fragment>
            ))}
          </BreadcrumbList>
        </Breadcrumb>
      </div>
      <UserNav />
    </header>
  );
}
