
"use client";

import React from 'react';
import Link from 'next/link';
import { usePathname } from 'next/navigation';
import {
  SidebarHeader,
  SidebarContent,
  SidebarMenu,
  SidebarMenuItem,
  SidebarMenuButton,
  SidebarFooter,
} from '@/components/ui/sidebar';
import {
  Users,
  Building,
  CalendarCheck,
  ClipboardList,
  LayoutDashboard,
  Workflow,
  DollarSign,
} from 'lucide-react';
import { useUser } from '@/contexts/user-provider';

const navItems = [
  { href: '/dashboard', label: 'Dashboard', icon: LayoutDashboard, roles: ['Admin', 'Manager', 'Employee', 'HR Staff'] },
  { href: '/dashboard/employees', label: 'Employees', icon: Users, roles: ['Admin', 'Manager', 'Employee', 'HR Staff'] },
  { href: '/dashboard/departments', label: 'Departments', icon: Building, roles: ['Admin'] },
  { href: '/dashboard/leave', label: 'Leave', icon: CalendarCheck, roles: ['Admin', 'Manager', 'Employee', 'HR Staff'] },
  { href: '/dashboard/attendance', label: 'Attendance', icon: ClipboardList, roles: ['Admin', 'Manager', 'Employee', 'HR Staff'] },
  { href: '/dashboard/payroll', label: 'Payroll', icon: DollarSign, roles: ['Admin', 'Manager', 'HR Staff', 'Employee'] },
];

export function DashboardNav() {
  const pathname = usePathname();
  const { user } = useUser();

  if (!user) return null;

  const filteredNavItems = navItems.filter(item => item.roles.includes(user.role));

  return (
    <>
      <SidebarHeader>
        <div className="flex items-center gap-2 p-2">
          <Workflow className="w-8 h-8 text-primary" />
          <h1 className="text-xl font-bold font-headline">EMS Vision</h1>
        </div>
      </SidebarHeader>
      <SidebarContent>
        <SidebarMenu>
          {filteredNavItems.map((item) => (
            <SidebarMenuItem key={item.href}>
              <Link href={item.href} passHref>
                <SidebarMenuButton
                  asChild
                  isActive={pathname === item.href}
                  tooltip={item.label}
                >
                  <span>
                    <item.icon />
                    <span>{item.label}</span>
                  </span>
                </SidebarMenuButton>
              </Link>
            </SidebarMenuItem>
          ))}
        </SidebarMenu>
      </SidebarContent>
      <SidebarFooter>
      </SidebarFooter>
    </>
  );
}
